<template>
<div id="app">
    <!--顶部广告条-->
    <div class="flex-row tbar" v-show="menuVisible">
        <img :src="album.producer.icon | img"/>
        <span v-show="album.close_ad != '1'">{{album.producer.username}}</span>
        <span v-show="album.close_ad != '1'">{{album.producer.phone}}</span>
    </div>

    <!--book容器-->
    <div class="content" :style="{'background-image':'url('+album.background+')'}" v-show="loaded">
        <div ref="ct" @click="menuVisible = !menuVisible">
            <div v-for="pic of album.pic" class="pic" :style="{'background-image':'url('+pic.url+')'}">

            </div>
        </div>
    </div>

    <!--页面列表-->
    <div class="list menu" v-if="menu === 'list'">
        <div class="list-cont">
            <div v-for="(pic, index) of album.pic" class="list-item" @click="toPage(index, pic, true)">
                <img :src="pic.thumbnail"/>
                <div class="page-txt">
                    {{index + 1}}
                </div>
            </div>
        </div>
    </div>

    <!--翻页和背景开关-->
    <div class="xtoggle menu menu-bar" v-if="menu === 'music'">
        <div class="check-group">翻页开关<span class="xcheck" :class="{'active':pageSwitch}" @click="pageSwitch = !pageSwitch"></span></div>
        <div class="check-group">背景开关<span class="xcheck" :class="{'active':bgSwitch}" @click="bgSwitch = !bgSwitch"></span></div>
    </div>

    <!--分页条-->
    <div class="xpage menu menu-bar" v-if="menu === 'page'">
        <div class="xpage-icon first" @click="toPage(0)"></div>
        <div class="xpage-icon pre" @click="toggle(-1)"></div>
        <div class="xpage-txt"><span>{{num}}</span><span>/</span><span>{{total}}</span></div>
        <div class="xpage-icon next" @click="toggle(1)"></div>
        <div class="xpage-icon last" @click="toPage(total - 1)"></div>
    </div>

    <!--底部按钮条-->
    <div class="flex-row bbar" v-show="menuVisible">
        <div class="bbar-btn" @click="active('list')">
            <div class="icon icon-list" :class="{active: menu === 'list'}"></div>
        </div>
        <div class="bbar-btn" @click="active('page')">
            <div class="icon icon-page" :class="{active: menu === 'page'}"></div>
        </div>
        <div class="bbar-btn" @click="active('music')">
            <div class="icon icon-music" :class="{active: menu === 'music'}"></div>
        </div>
        <div class="bbar-btn" @click="play = !play">
            <div class="icon icon-play" :class="{active: play}"></div>
        </div>
    </div>

    <div style="display:none;">
        <audio :src="album.music" autoplay="true" loop="true" id="audio"></audio>
        <audio src="/static/albums/fy.mp3" id="fy"></audio>
    </div>
</div>
</template>

<script>

document.addEventListener('DOMContentLoaded', function () {
    function audioAutoPlay() {
        var bg = document.getElementById('audio');

        document.addEventListener("WeixinJSBridgeReady", function () {
            bg.play();
        }, false);
    }

    audioAutoPlay();
});

import Vue from 'vue';
const server = 'https://fitment.chenyoujiu.com';
Vue.filter('img', function(img){
    if(!img){
        return null;
    }
    return server + img;
})

export default {
	name: 'App',

    data(){
        return {
            id: null,
            width: null,
            height: null,
            loaded: false,
            num: 0,
            total: 0,
            play: false,
            menu: null,

            menuVisible: true,
            wxCfged: false,
            loaded: false,

            pageSwitch: true, // 翻页开关
            bgSwitch: true, // 背景开关

            album: {
                background: '',
                title: '',
                music: '',
                share_content: '',
                share_icon: '',
                close_share: '',
                close_ad: '',
                pic: [],
                producer: {
                    username: '',
                    icon: '',
                    phone: ''
                }
            }
        }
    },
    mounted(){
        const params = this.findParams();
        this.id = params.id;
        this.width = Number(params.width);
        this.height = Number(params.height);

        if(!this.id){
            return ;
        }
        try{
            document.getElementById('fy').load();
        }catch(e){}
        this.load();
        this.loadWXCfg();

    },

    methods: {
        findParams(){
            const params = {};
            let search = window.location.search;
            if(search){
                search = search.substring(1);
                const paramPair = search.split('&');

                for(let str of paramPair){
                    const pair = str.split('=');
                    params[pair[0]] = pair[1];
                }
            }
            return params;
        },

        // 加载数据
        load(){
            $.post('/picApi/factoryAlbumInfo', {
                id: this.id
            }, (resp)=>{
                if(!resp.code){
                    this.render(resp.result.album);
                    this.loaded = true;
                    this.initShare();

                    this.$nextTick(()=>{
                        document.getElementById('audio').play();

                        $(window).on('touchstart', ()=>{
                            if(this.bgSwitch){
                                document.getElementById('audio').play();
                            }

                            if(this.pageSwitch){
                                document.getElementById('fy').load();
                            }
                        });
                    });

                    window.onorientationchange = ()=>{
                        // alert($(window).width());
                        // const pic = album.pic;
                        // album.pic = [];
                        // $.$nextTick(()=>{
                        //     album.pic = album.pic;
                        //     $.$nextTick(()=>{
                        //         this.initTurn();
                        //     });
                        // });

                        setTimeout(()=>{
                            this.resize();
                        }, 500);


                    };
                }
            });
        },

        loadWXCfg: function(){
            ///wxApi/shareConfig
            $.post('/wxApi/shareConfig', null, (resp)=>{
                const result = resp.result[0];
                wx.config({
                    // debug: true,
                    appId: result['appId'],
                    timestamp: result['timestamp'],
                    nonceStr: result['nonceStr'],
                    signature: result['signature'],
                    jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
                });
                const that = this;
                wx.ready(()=>{
                    that.wxCfged = true;
                    that.initShare();
                });
                wx.error(function(res){
                    console.log(res);
                })
            });
        },

        // 渲染
        render(album){
            document.title = album.title;
            if(!album.background){
                album.background = '/static/albums/album-bg.png';
            }
            if(!album.share_icon){
                album.share_icon = '/static/albums/album-icon.png';
            }
            if(!album.share_content){
                album.share_content = '新款画册发布';
            }
            if(!album.music){
                album.music = '/static/albums/bg.mp3';
            }

            for(let pic of album.pic){
                pic.thumbnail = server + pic.thumbnail;
                pic.url = server + pic.url;
            }
            this.album = album;
            this.loaded = true;

            this.$nextTick(()=>{
                this.initTurn();
            });

        },



        calSize(){
            const el = $($(this.$refs.ct));
            const body = $(window);//$(document.body);
            const thuH = this.album.pic[0].thumbnail_height;
            const thuW = this.album.pic[0].thumbnail_width;

            let bodyW = body.width() - 10;
            let bodyH = body.height();
            let display = null;
            let w = null;
            let h = null;

            // 打横模式
            if(bodyW > bodyH){
                display = 'double';
                w = bodyW - 20;
                h = thuH/thuW*w/2;

                if(h > bodyH){
                    h = bodyH - 20;
                    w = thuW/thuH*h*2;
                }
            }else{
                display = 'single';
                w = bodyW - 10;
                h = thuH/thuW*w;

                if(h > bodyH){
                    h = bodyH - 10;
                    w = thuW/thuH*h;
                }
            }

            return {display, w, h};
        },

        resize(){
            const size = this.calSize();
            const el = $(this.$refs.ct);
            el.turn('size', size.w, size.h);
            el.turn('display', size.display);
            el.turn('resize');
        },

        initTurn(){
            const size = this.calSize();

            const that = this;
            $(this.$refs.ct).turn({
                width: size.w,
                height: size.h,
                display: size.display,
                autoCenter: true,
                when: {
                    turning(){
                        if(that.pageSwitch){
                            var audio = document.getElementById('fy');
                            audio.load();
                            audio.play();
                        }
                    }
                }
            });
            if(this.play){
                this.start();
            }
        },

        active(menu){
            if(this.menu === menu){
                this.menu = null;
            }else{
                this.menu = menu;
            }
        },

        toPage(index, pic, hide){
            if(index < 0 || index >= this.album.pic.length){
                return ;
            }
            const page = index + 1;
            if(hide){
                this.menu = null;
            }
            $(this.$refs.ct).turn('page', page);
            this.num = page;
        },

        toggle(add){
            this.toPage($(this.$refs.ct).turn('page') + add - 1);
        },

        stop(){
            if(this.timer){
                clearInterval(this.timer);
            }
            this.timer = null;
        },

        start(){
            this.stop();
            this.timer = setInterval(()=>{
                this.nextPage();
            }, 3000);
        },

        nextPage(){
            let page = $(this.$refs.ct).turn('page');
            if(page >= this.album.pic.length){
                page = 1;
            }else{
                page++;
            }

            this.toPage(page - 1);
        },

        turnPlay(){
            if(this.play){
                this.nextPage();
                this.start();
            }else{
                this.stop();
            }
        },

        initShare(){
            if(!this.loaded || !this.wxCfged){
                return ;
            }
            console.log('init share');
            const link = window.location.href.replace(/#\S*/,"");
            const title = this.album.title;
            const desc = this.album.share_content || title;
            const imgUrl = window.location.origin + this.album.share_icon;
            // 分享到朋友圈
            wx.onMenuShareTimeline({
                title: desc, // 分享标题
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () {}
            });

            // 分享给朋友
            wx.onMenuShareAppMessage({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接，该链接域名必须与当前企业的可信域名一致
                imgUrl: imgUrl, // 分享图标
                type: 'link', // 分享类型,music、video或link，不填默认为link
                success: function () {

                },
                cancel: function () {

                }
            });

            wx.onMenuShareQQ({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });

            wx.onMenuShareWeibo({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });

            wx.onMenuShareQZone({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });
        }

    },

    watch: {
        menu(val){
            if(val === 'page'){
                this.num = $(this.$refs.ct).turn('page');
                this.total = this.album.pic.length;
            }
        },

        bgSwitch(val){
            if(!this.album.music){
                return;
            }

            var audio = document.getElementById('audio');
            if(val){
                audio.play();
            }else{
                audio.pause();
            }
        },

        play(){
            this.turnPlay();
        },

        menuVisible(){
            if(!this.menuVisible){
                this.menu = null;
            }
        }
    }
}
</script>

<style>
html,body,#app{
    width:100%;
    height:100%;
    padding:0;
    margin:0;
    overflow:hidden;
}
#app{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
}

.content{
    overflow:hidden;
    width:100%;
    height:100%;
    background-repeat:no-repeat;
    background-size:100% 100%;
    display:flex;
    justify-content:center;
    align-items:center;
    z-index:100;
}
.flex-row, .flex-col{
    display:flex;
    align-items:center;
}

.flex-row{
    flex-direction:row;
}

.flex-col{
    flex-direction:column;
}

.tbar, .bbar{
    position:fixed;
    left:0;
    width:100%;
    z-index:99999;
}

.tbar{
    top:0;
}
.bbar{
    bottom:0;
}

.tbar{
    justify-content:flex-end;
    height:20px;
    background-color:rgba(42, 42, 42, 0.8);
    font-size:12px;
    line-height:12px;
    color:#979A9C;
}

.tbar > *{
    margin-right:18px;
}

.tbar img{
    width:12px;
    height:12px;
    border-radius:7px;
}


.pic{
    background-repeat:no-repeat;
    background-position:center;
    background-size:contain;
    width:100%;
    height:100%;
    background-color:rgba(255,255,255,0.5);
}

.bbar{
    height:44px;
    justify-content:space-around;
    background-color:rgba(42, 42, 42, 0.9);
}


.bbar-btn{
    padding-top:10px;
    padding-bottom:14px;
    padding-left:14px;
    padding-right:14px;
}

.icon{
    width:20px;
    height:20px;
    background-repeat:no-repeat;
    background-size:100% 100%;
}

.icon-list{
    background-image:url('/static/albums/album-rect.png?v1');
}
.icon-list.active{
    background-image:url('/static/albums/album-rect-on.png?v1');
}
.icon-page{
    background-image:url('/static/albums/album-lr.png?v1');
}
.icon-page.active{
    background-image:url('/static/albums/album-lr-on.png?v1');
}
.icon-music{
    background-image:url('/static/albums/album-music.png?v1');
}
.icon-music.active{
    background-image:url('/static/albums/album-music-on.png?v1');
}
.icon-play{
    background-image:url('/static/albums/album-play.png?v1');
}
.icon-play.active{
    background-image:url('/static/albums/album-stop.png?v1');
}

.list{
    top:0;
    display:flex;
    flex-flow:column;
    justify-content:flex-end;
    overflow-y:auto;
    -webkit-overflow-scrolling: touch;
}

.menu{
    position:fixed;
    left:0;
    bottom:44px;
    width:100%;
    background:rgba(42, 42, 42, 0.9);
    z-index:999999;
}

.list-cont{
    display:flex;
    flex-direction:row;
    justify-content:space-around;
    flex-wrap:wrap;
    align-items:center;
    -webkit-overflow-scrolling:touch;
}

.list-item{
    padding:20px 23px;
}

.list-item img{
    width:46px;
}

.menu-bar{
    height:56px;
    display:flex;
    justify-content:center;
    align-items:center;
}

.xpage-txt, .xpage-icon{
    height:30px;
    line-height:30px;
}

.xpage-txt{
    background:#fff;
    padding:0 6px;
}

.xpage-txt > span{
    padding:0 6px;
}

.page-txt{
    text-align:center;
    color:#fff;
}

.xpage-icon{
    background-repeat:no-repeat;
    background-size:100% 100%;
    width:30px;
}

.xpage-icon.first{
    background-image:url("/static/albums/album-first.png?v1");
}
.xpage-icon.pre{
    background-image:url("/static/albums/album-pre.png?v1");
}
.xpage-icon.next{
    background-image:url("/static/albums/album-next.png?v1");
}
.xpage-icon.last{
    background-image:url("/static/albums/album-last.png?v1");
}

.xpage > *{
    margin:0 10px;
    border:1px solid #727277;
}

.menu-bar input{
    display:inline-block;
    width:20px;
    height:20px;
}

.xcheck{
    margin-left:5px;
    margin-right:30px;
    display:inline-block;
    width:20px;
    height:20px;
    background-size:100% 100%;
    background-image:url("/static/albums/album-check-empty.png?v1");
}

.xcheck.active{
    background-image:url("/static/albums/album-check-ok.png?v1");
}

.xtoggle > *{
    color:#3D3C50;
}

.check-group{
    display:flex;
    flex-flow:row;
    justify-content:center;
    align-items:center;
    color:#727277;
}

.none-bg{
    background:none!important;
}
</style>



